<template>
    <div class="page-container">
        <div class="memberSet-container">
            <div class="memberSet-list">
                <div class="memberSet-list-header">
                    <div class="memberSet-title" style="padding:3px 0 0 0;">SNS 로그인</div>
                </div>
                <div class="memberSet-list-body">
                    <ul v-for="(item,key) in snsList" class="main-memberSet">
                        <li class="main-li">
                            <div :class="'main-data '+item.active" style="cursor: pointer;" @click="choiceSns(key)">
                                <div class="gname">
                                    {{ item.name }}
                                    <font-awesome-icon v-if="item.use=='yes'" icon="fa-solid fa-eye"/>
                                    <font-awesome-icon v-else icon="fa-solid fa-eye-slash"/>
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="memberSet-regist">
                <div  v-if="showKey">
                    <div class="memberSet-title">SNS 정보 설정</div>
                    <form novalidate @submit.prevent="validate">
                        <div>
                            <div class="memberSet-title" style="font-size: 14px;padding-bottom:10px;">
                                {{snsName}} 정보
                            </div>
                            <ul class="table-ul">
                                <li>
                                    <div class="label-input">
                                        <div class="input-box">
                                            <v-text-field v-model="snsKey"
                                                          ref="snsKey"
                                                          dense
                                                          :rules="[fieldRequired]"
                                                          outlined
                                                          label="[필수]  REST API KEY"
                                                          placeholder=" REST API KEY를 입력해주세요"/>
                                        </div>
                                    </div>

                                </li>
                                <li>
                                    <div class="label-input">
                                        <div class="input-box">
                                            <v-text-field v-model="snsSecret"
                                                          dense
                                                          ref="snsSecret"
                                                          outlined
                                                          :rules="[secretRequired]"
                                                          label="SCRIPT KEY"
                                                          placeholder=" KEY를 입력해주세요"/>
                                        </div>
                                    </div>

                                </li>
                                <li>
                                    <div class="label">
                                        {{snsName}}  사용여부
                                    </div>
                                    <div class="label-input">
                                        <div class="radio-box">
                                            <v-radio-group
                                                    v-model="snsUse"
                                                    dense
                                                    row
                                            >
                                                <v-radio
                                                        label="사용"
                                                        value="yes"
                                                ></v-radio>
                                                <v-radio
                                                        label="사용안함"
                                                        value="no"
                                                ></v-radio>
                                            </v-radio-group>
                                        </div>
                                    </div>
                                </li>
                            </ul>
                            <div class="callback-url">
                                <div style="font-size:14px;">
                                    CALL BACK URL
                                    <v-tooltip bottom>
                                        <template v-slot:activator="{ on, attrs }">
                                            <font-awesome-icon v-bind="attrs"
                                                               v-on="on" class="help-icon"
                                                               icon="fa-solid fa-circle-question"/>
                                        </template>
                                        <span>
                                                SNS업체 API 설정 정보에 하단 URL을 입력하세요<br>
                                                로그인 CALL_BACK_URL, 조인 CALL_BACK_URL 동일합니다
                                            </span>
                                    </v-tooltip>
                                </div>
                                <div style="font-size:14px;font-weight:bold;padding-top:9px;"> {{
                                    snsCallBackUrl
                                    }}
                                </div>

                            </div>

                        </div>
                        <div class="button-row">
                            <v-btn class="submit-button" type="submit"> SNS정보 저장</v-btn>
                        </div>
                    </form>
                </div>
            </div>
        </div>


    </div>
</template>


<script>
    import apiService from '@/config/admin/apiService'
    import {
        API_ADMIN_UPDATE_SETTING_MEMBER,
        API_ADMIN_GET_SETTING_MEMBER
    } from '@/config/admin/urls'

    export default {
        computed: {
            form () {
                return {
                    snsKey: this.snsKey,
                    snsSecret: this.snsSecret,
                }
            },
        },
        data() {
            return {
                serverFlag: false,
                snsName:'',
                snsUse: 'yes',
                snsKey: '',
                snsSecret: '',
                snsList: {
                    'ka': {name: '카카오', active: '', use: 'no', key: '', secret: '',secretRequired:false},
                    'fb': {name: '페이스북', active: '', use: 'no', key: '', secret: '',secretRequired:true},
                    'nv': {name: '네이버', active: '', use: 'no', key: '', secret: '',secretRequired:true},
                    'gl': {name: '구글', active: '', use: 'no', key: '', secret: '',secretRequired:true}
                },
                showKey: '',
                redirectUrls: {},
                snsCallBackUrl: '',
                secretReq:false,
                fieldRequired: value => !!value || '필수입력 항목',
            };
        },

        created() {
            this.getData();
        },
        methods: {
            getData() {
                if (this.serverFlag) return;
                this.serverFlag = true;

                apiService.post(API_ADMIN_GET_SETTING_MEMBER, {}, (res) => {
                    this.serverFlag = false;
                    if (res.setting && res.setting.sns) {
                        const snsList = JSON.parse(res.setting.sns);
                        for(let key in snsList) {
                            if(snsList[key]) {
                                this.snsList[key] = snsList[key];
                                this.snsList[key].active = '';
                            }

                        }

                    }

                })
            },
            choiceSns(id) {

                for(const key in this.snsList) {
                    this.snsList[key].active = '';
                }
                let item = this.snsList[id];
                 console.log(item)
                item.active = 'active';
                this.showKey = id;
                this.snsName = item.name;
                this.snsKey = item.key;
                this.snsSecret = item.secret;
                this.snsUse = item.use;
                this.secretReq = item.secretRequired

                let siteDomain = this.getSiteDomain();
                this.snsCallBackUrl = siteDomain + '/api/controller/snsCallBack/' + id;

            },
            secretRequired(val) {
                if(this.secretReq) {
                    if(!val)return '키값을 입력하세요';
                }
                return true;
            },
            getSiteDomain() {
                let urls = location.hostname.split('.');
                let siteDomain;
                if (urls[0] == 'admin') {
                    siteDomain = location.protocol + '//' + location.hostname.replace('admin.', '');
                } else {
                    siteDomain = location.protocol + '//' + location.hostname;

                }
                return siteDomain;

            },
            validate() {
                let formHasErrors = false;
                let choiceItem = this.snsList[this.showKey];
                Object.keys(this.form).forEach(f => {
                    let check = false;
                    switch(f) {
                        case 'snsSecret':
                            if(!choiceItem.secretRequired) {
                                check=true;
                            }
                            break;
                    }
                    if(!check) {
                        if (!this.form[f]) {
                            formHasErrors = true
                        }
                        this.$refs[f].validate(true)
                    }
                })

                if(formHasErrors)return;
                choiceItem.use = this.snsUse;
                choiceItem.key =this.snsKey;
                choiceItem.secret = this.snsSecret;
                let frm = {
                    sns:JSON.stringify(this.snsList),
                }
                if (this.serverFlag) return;
                this.serverFlag = true;
                this.$eventBus.$emit('overlay', 'open');
                apiService.post(API_ADMIN_UPDATE_SETTING_MEMBER, frm, (res) => {
                    this.serverFlag = false;
                    this.$eventBus.$emit('overlay', 'hide');
                    let params = {
                        message: 'SNS정보가 저장 되었습니다'
                    }
                    this.$eventBus.$emit('modalOpen', params);


                })

            }

        }

    }
</script>

